<template>
  <div class="secure">
    <div class="topBgcolor"></div>

    <!-- 导航条 -->
    <van-nav-bar
      title="安全中心"
      left-text="返回"
      left-arrow
      :fixed='true'
      @click-left="back"
    />

    <!-- 头部背景图 -->
    <div class="my-bg">
      <div class="bg auto-img">
      </div>
    </div>

    <div class="secure-box">
      <div>
        <van-cell
          title="修改密码"
          isLink
          @click="openPasswordPopup"
        />
        <van-cell
          title="注销账号"
          isLink
          @click="destroyAcount"
        />
        <van-cell
          title="退出登录"
          isLink
          @click="logout"
        />
      </div>
    </div>

    <!-- 修改密码弹出层 -->
    <van-popup
      v-model="show"
      position="bottom"
      closeable
      round
      class="reg-box"
    >
      <!-- 表单 -->
      <div class="message-box">
        <h2>修改密码</h2>
        <van-form>
          <van-field
            v-for="(item,index) in passwords"
            :key="index"
            class="border-bottom"
            :label="item.title"
            :type="item.isPassword ? 'password' : 'text'"
            placeholder="6-16位密码且以字母开头"
            v-model="item.password"
            :right-icon="item.isPassword ? 'closed-eye' : 'eye-o'"
            @click-right-icon="viewpassword(item)"
          />
          <div class="register-btn">
            <van-button
              type="info"
              native-type="submit"
              color="#0b34ba"
              @click="confirmPassword"
            >确认修改</van-button>
          </div>
        </van-form>
      </div>

    </van-popup>

  </div>
</template>

<script>
import "../assets/less/secure.css";
import { utils } from "../assets/js/utils";
import { validForm } from "../assets/js/validForm";

export default {
  data() {
    return {
      show: false,
      passwords: {
        old: {
          password: "",
          isPassword: true,
          title: "旧密码：",
        },
        new: {
          password: "",
          isPassword: true,
          title: "新密码：",
        },
      },
    };
  },

  created() {
    this.islogin();
  },

  methods: {
    // 跳转上一步
    back() {
      this.$router.go(-1);
    },

    // 回到菜单页面
    goMenu() {
      this.$router.push({ name: "Menu" });
    },

    // 验证token
    // 获取token字符串
    islogin() {
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        this.$toast("请先登录");
        return this.$router.push({ name: "Login" });
      }
    },

    // 查看密码
    viewpassword(item) {
      item.isPassword = !item.isPassword;
    },

    // 展示修改密码弹出层
    openPasswordPopup() {
      // 获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        this.$toast("请先登录");
        return this.$router.push({ name: "Login" });
      }
      this.show = true;
    },

    // 确定修改密码
    confirmPassword() {
      // 获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return this.$router.push({ name: "Login" });
      }

      let o = {
        oldPassword: {
          value: this.passwords.old.password,
          reg: /^[A_Za-z]\w{5,15}$/,
          errorMsg: "支持6-16位密码",
        },
        password: {
          value: this.passwords.new.password,
          reg: /^[A_Za-z]\w{5,15}$/,
          errorMsg: "支持6-16位密码",
        },
      };

      // 验证表单
      if (!validForm.valid(o)) {
        //验证不通过
        return;
      }

      // 发起修改密码请求
      //   发起请求
      // 开启加载提示
      this.$toast.loading({
        message: "加载中...",
        // 防止穿透
        forbidClick: true,
        // 图标
        loadingType: "spinner",
        // 显示时间，如果时间位0，则不会自动关闭
        duration: 0,
      });

      //   序列化参数
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        oldPassword: this.passwords.old.password,
        password: this.passwords.new.password,
      });

      this.axios({
        method: "POST",
        url: "/updatePassword",
        data,
      })
        .then((result) => {
          //   关闭加载提示;
          this.$toast.clear();
          

          if (this.passwords.old.password == this.passwords.new.password) {
            return this.$toast("新密码与旧密码不能相同！");
          }

          if (result.data.code == "E001") {
            localStorage.removeItem("CSTK");
            this.$toast("修改成功，请重新登录");
            this.$router.push({ name: "Login" });
          } else {
            this.$notify({
              type: "warning",
              message: result.data.msg,
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    // 注销账号
    destroyAcount() {
      // 获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        this.$toast("请先登录");
        return this.$router.push({ name: "Login" });
      }

      this.$dialog
        .confirm({
          title: "注销账号",
          message: "是否确定注销账号，一旦确认注销则无法恢复",
          confirmButtonColor: "#0b34ba",
        })
        .then(() => {
          // 
          // 获取token字符串
          let tokenString = localStorage.getItem("CSTK");
          if (!tokenString) {
            this.$toast("请先登录");
            return this.$router.push({ name: "Login" });
          }

          //   发起请求
          this.$toast.loading({
            message: "加载中...",
            // 防止穿透
            forbidClick: true,
            // 图标
            loadingType: "spinner",
            // 显示时间，如果时间位0，则不会自动关闭
            duration: 0,
          });

          //   序列化参数
          let data = utils.queryString({
            appkey: this.appkey,
            tokenString,
          });

          this.axios({
            method: "POST",
            url: "/destroyAccount",
            data,
          })
            .then((result) => {
              //   关闭加载提示;
              this.$toast.clear();
              
              if (result.data.code == "G001") {
                localStorage.removeItem("CSTK");
                this.$toast("注销成功，即将返回登录");
                setTimeout(() => {
                  this.$router.push({ name: "Login" });
                }, 2000);
              } else {
                this.$notify({
                  type: "warning",
                  message: result.data.msg,
                });
              }
            })
            .catch((err) => {
              this.$toast.clear();
              
            });
        })
        .catch(() => {
          
        });
    },

    // 退出登录
    logout() {
      // 获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        this.$toast("请先登录");
        return this.$router.push({ name: "Login" });
      }

      this.$dialog
        .confirm({
          title: "退出登录",
          message: "是否确定退出登录？",
          confirmButtonColor: "#0b34ba",
        })
        .then(() => {
          this.$toast.loading({
            message: "加载中...",
            forbidClick: true,
            duration: 0,
            loadingType: "spinner",
          });

          localStorage.removeItem("CSTK");
          setTimeout(() => {
            this.$toast.clear();
            this.$router.push({ name: "Login" });
          }, 1000);
        })
        .catch(() => {
          
        });
    },
  },
};
</script>